<template>
  <div id='app'>
    <div class='app-phone'>
      <div class='phone-header'>
        <img src='http://cjs00wo.dothome.co.kr/instagram/img/instargramlogo.png' alt='logo'>
        <!-- cancleÏùÑ ÌÅ¥Î¶≠ÌñàÏùÑÎïå goToHomeÏù¥ÎùºÎäî Ìï®ÏàòÎ°ú Í∞ÄÎùº -->
        <a class='cancel-cta' v-if='step===2 || step===3' @click='goToHome, step--'>Cancel</a>
        <a class='next-cta' v-if='step===2' @click='step++'>Next</a>
        <a class='next-cta' v-if='step===3' @click='sharePost'>Share</a>
      </div>
      <phone-body :step='step' :posts='posts' :filters='filters' :image='image' :selectedFilter='selectedFilter' v-model='caption'>
        <!-- v-modelÏùò Í∞íÏùÄ captionÏúºÎ°ú ÌïòÏûê. -->
      </phone-body>
      <div class='phone-footer'>
        <div class='home-cta' @click='goToHome'>
          <i class='fas fa-home'></i>
        </div>
        <div class='upload-cta'>
          <input type='file' name='file' id='file' class='inputfile' @change='uploadImage' :disabled='step!==1'>
          <!-- @change='uploadImage' changeÎùºÎäî Ïù¥Î≤§Ìä∏Í∞Ä Î∞úÏÉùÌïòÎ©¥ uploadImage Ìï®Ïàò Ìò∏Ï∂úÌï¥Îùº @change - ÎÇ¥Í∞Ä Î¨¥ÏóáÏùÑ ÏÑ†ÌÉùÌïòÎäîÏßÄÏóê Îî∞Îùº Î∞îÎÄåÎäî Í≤É :disabled='step!==1' stepÍ∞íÏù¥ 1Ïù¥ ÏïÑÎãåÍ≤ΩÏö∞ ÎπÑÌôúÏÑ±ÌôîÎêòÎùº-->
          <label for='file'>
            <i class='fas fa-plus-square fa-lg'></i>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PhoneBody from './components/PhoneBody.vue'
import posts from './data/posts.js'
import filters from './data/filters.js'

import EventBus from './event-bus.js'

export default {
  name: 'App',
  data () {
    return {
      step: 1,
      posts,
      filters,
      image: '', // Ïù¥ÎØ∏ÏßÄÎ•º ÏÑ†ÌÉùÌñàÏùÑÎïå Í∑∏ Ïù¥ÎØ∏ÏßÄ Í∞íÏù¥ Ï†ÄÏû•ÎêòÎäî Í≥≥
      selectedFilter: '',
      caption: ''
    }
  },
  created () {
    // Ïù¥Î≤§Ìä∏Î•º Î∞õÏùÑÎïå
    // v-on:Ïù¥Î≤§Ìä∏Ïù¥Î¶Ñ='Ìï®ÏàòÏù¥Î¶Ñ'
    // ÏßÅÏ†ë Ìï≠ÏàòÎ•º Ï†ÅÏö©Ìï†ÎïåÎäî ÏïÑÎûòÏôÄ Í∞ôÏù¥
    // EventBus.$on('Ïù¥Î≤§Ìä∏ Ïù¥Î¶Ñ', Îß§Í∞úÎ≥ÄÏàò => Ïã§ÌñâÎ¨∏)
    EventBus.$on('filter-selected', evt => {
      this.selectedFilter = evt.filter
    })
  },
  methods: {
    // Ìï®Ïàò
    // cancleÎ≤ÑÌäº ÎàåÎ†ÄÏùÑÎïå Ïã§ÌñâÎêòÎäî Ìï®Ïàò
    goToHome () {
      // ÌòÑÏû¨ Ïª¥Ìè¨ÎÑåÌä∏Ïùò Ïù¥ÎØ∏ÏßÄ, ÌïÑÌÑ∞Ï†ÅÏö©, Ï∫°ÏÖò ÏÇ¨ÎùºÏßÄÍ≥† stepÏùÄ 1Î°ú Í∞ÄÎùº.
      this.image = ''
      this.selectedFilter = ''
      this.caption = ''
      this.step = 1
    },

    // step 2ÏóêÏÑú shareÎ≤ÑÌäº ÎàåÎ†ÄÏùÑÎïå
    sharePost () {
      const post = {
        username: 'goeun_y', // Í≥†Ï†ï
        userImage:
          'http://cjs00wo.dothome.co.kr/instagram/img/user1.jpg', // Í≥†Ï†ï
        postImage: this.image, // ÏÑ†ÌÉùÌïú Ïù¥ÎØ∏ÏßÄ
        likes: 0,
        caption: this.caption,
        filter: this.selectedFilter
      }
      // .unshift() Î∞∞Ïó¥Ïùò Îß®ÏúÑÏóê Ï≤´Î≤àÏß∏Î°ú ÎÑ£ÎäîÍ≤É
      this.posts.unshift(post)
      this.goToHome()
    },

    uploadImage (evt) {
      // filesÎùºÎäî ÏÉÅÏàòÏóêÎã§Í∞Ä ÌÅ¥Î¶≠Ìïú Ïù¥Î≤§Ìä∏Î•º Ï†ÄÏû•Ìï¥Îùº.
      const files = evt.target.files
      // returnÏù¥ Ïã§ÌñâÎêòÎ†§Î©¥ ()Í∞íÏù¥ trueÏó¨ÏïºÌïúÎã§.
      // files.lengthÍ∞Ä falseÏùºÎïåÎäî ÌååÏùºÏùò Í∞íÏù¥ ÏóÜÏùÑÎïåÏù¥Îã§.
      if (!files.length) return

      // ÌååÏùºÏùò Í∞ØÏàòÍ∞Ä ÌïòÎÇò Ïù¥ÏÉÅÏùºÎïå ÏïÑÎûòÏùò Ïã§ÌñâÎ¨∏Ïù¥ Ïã§ÌñâÎèºÎùº

      // FileReader() ÌååÏùºÏùÑ ÏùΩÏñ¥Ï£ºÎäî ÎÇ¥Ïû•Ìï®Ïàò
      const reader = new FileReader()
      reader.readAsDataURL(files[0])
      reader.onload = evt => {
        // Ïù¥Î≤§Ìä∏ Í∞ùÏ≤¥Ïùò ÌÉÄÍ≤üÏù¥ ÎêòÎäî Í≤∞Í≥ºÍ∞íÏùÑ Ïù¥ÎØ∏ÏßÄÏóê ÎÑ£Ïñ¥Ï§òÎùº
        this.image = evt.target.result
        this.step = 2
      }
      document.querySelector('#file').value = ''
    }
  },
  components: {
    'phone-body': PhoneBody
  }
}
</script>

<style lang='scss' src='./styles/app.scss'>
</style>
