// Î∑∞
<template>
  <div class="vuegram-post">
    <div class="header level">
      <div class="level-left">
        <figure class="image is-32x32">
          <img :src="post.userImage">
        </figure>
        <span class="username">{{post.username}}</span>
      </div>
    </div>

    <!-- Ïù¥ÎØ∏ÏßÄ Îì§Ïñ¥Í∞ÄÎäî Î∂ÄÎ∂Ñ -->
    <div class="image-container" :class="post.filter" :style=" {backgroundImage:'url('+post.postImage+')'}" @dblclick="like"></div>
    <!-- Í∏ÄÏî® Îì§Ïñ¥Í∞ÄÎäî Î∂ÄÎ∂Ñ -->
    <div class = 'content'>
      <div class = 'heart'>
        <i class = 'far fa-heart fa-lg' :class = "{'fas':this.post.hasBeenLiked}" @click="like"></i> <!-- ÌïòÌä∏Í∞Ä ÎàåÎü¨Ï°åÏùÑÎïå fasÎûÄ ÌÅ¥ÎûòÏä§Í∞Ä Ï∂îÍ∞ÄÎêúÎã§ -->
      </div>
      <p class="likes">{{post.likes}} likes</p> <!-- {{}}ÏΩßÏàòÏóΩ - Îç∞Ïù¥ÌÑ∞Î∞îÏù∏Îî©(Îç∞Ïù¥ÌÑ∞Í∞íÏùÑ ÌôîÎ©¥Ïóê ÎÇ¥Î≥¥ÎÇ¥ÎäîÍ≤É) -->
      <p class="caption">
        <span>{{post.username}}</span>
        {{post.caption}}
      </p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'VuegramPost',
  props: {
    post: Object // postÎäî Object(Í∞ùÏ≤¥)
  },
  methods: {
    like () {
      // ÏÇºÌï©Ïó∞ÏÇ∞Ïûê  Ï°∞Í±¥ ? Ï∞∏:Í±∞Ïßì  Ï∞∏Ïù¥Î©¥ ÏïûÏóêÏûàÎäîÍ±∞ Ïã§Ìñâ, Í±∞ÏßìÏù¥Î©¥ Îí§Ïóê ÏûàÎäî Í±∞ Ïã§Ìñâ

      // Ï¢ãÏïÑÏöîÍ∞Ä ÎàåÎ†§ÏûàÏùÑÎïå(true) ÎàÑÎ•¥Î©¥ --,ÏóÜÏùÑÎïåÎäî(false) ++
      this.post.hasBeenLiked ? this.post.likes-- : this.post.likes++
      // ÌòÑÏû¨ÏÉÅÌÉúÏùò Î∞òÎåÄÎ•º Ï†ÄÏû•Ìï¥Îùº. ÌïòÌä∏Í∞Ä ÏóÜÏùÑÎïê false, ÏûàÏùÑÎïê true
      this.post.hasBeenLiked = !this.post.hasBeenLiked
    }
  }
}
</script>
<style lang="scss" src="../styles/vuegram-post.scss"></style>
